body {
  margin: 0;
  background-color: #000000;
}

#concierge {
  display: none !important;
}

.main-mylt {
  background-color: #000000;
  height: 100vh;
  width: 75%;
  margin: 0 auto;
  padding: 1rem;
  overflow: hidden;
}

.tmp_logo-container {
  margin-bottom: 20px;
}

.tmp_img-ultrafit {
  float: right;
}

.tmp_container {
  position: relative;
  padding-bottom: calc(var(--aspect-ratio, .5625) * 100%);
  height: 0;
}

.tmp_container iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.tmp_overlay {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0%;
  right: 0%;
  top: 0%;
  bottom: 0%;
  margin: auto;
  background-size: contain;
  background-position: center;
  background-color: rgba(0, 0, 0, 0.25);
}

.tmp_playbutton {
  width: 256px;
  height: 256px;
  position: absolute;
  left: 0%;
  right: 0%;
  top: 0%;
  bottom: 0%;
  margin: auto;
}

.tmp_overlay {
  opacity: 1;
  transition: opacity 400ms;
}

.tmp_overlay[hidden] {
  opacity: 0;
  display: block !important;
}